window.onload = () => {
    document.getElementById("queriertest").addEventListener('dataReadyqueriertest', function (e) {
        let dataRetrieved = e.detail;
        console.log("AQUI ESTA", dataRetrieved)
        let city = document.createElement('a-entity');
        city.setAttribute('layout', 'type: box; columns: ' + dataRetrieved.length / 4 + '; margin: 5; plane: xz');
        dataRetrieved.forEach((index, item) => {
            let box = generateBox(index);
            city.appendChild(box)
        });
        document.getElementById("AframeScene").appendChild(city)

    });

    function generateBox(index) {
        let entity = document.createElement('a-entity');
        entity.setAttribute('geometry', 'primitive: box');
        entity.setAttribute('material', 'color: red');
        entity.setAttribute('visdata', 'from: queriertest; index:' + index);
        entity.setAttribute('vismapper', "width: open_issues; depth: open_issues; height: size");
        return entity;
    }

};